import React, { useEffect } from "react";
import {
  Card,
  Row,
  Col,
  Table,
  Space,
  Button,
  Typography,
  Pagination,
} from "antd";
import { connect } from "react-redux";
import Swal from "sweetalert2";
import { RiErrorWarningLine } from "react-icons/ri";
import {
  getStockTransferViewById,
  isStockApprovalModelVisible,
  getStockApprovaltList,
  setStockApprovalParams,
} from "../../../../../store/sideMenu/StockTransfer/StockRequest/stockrequestSlice";
import moment from "moment";

function StockTransferApprovalList(props) {
  const { stockApprovalList } = props;
  const { Text, Link } = Typography;

  // function stockInApprove(status, id) {
  //   var approveObj = {
  //     transferstatus: status,
  //   }
  //   props.stockTransferApprove({ approveObj, id })
  // }

  useEffect(() => {
    props.getStockApprovaltList({ ...props.stockApprovalparams, page: 1 });
  }, []);

  function onPageChange(page, pageSize) {
    props.setStockApprovalParams({
      ...props.stockApprovalparams,
      page_size: pageSize,
      page: page == 0 ? 1 : page,
    });
    props.getStockApprovaltList({
      ...props.stockApprovalparams,
      page_size: pageSize,
      page: page == 0 ? 1 : page,
    });
  }

  function showTotal(total) {
    return "Total " + total + " items";
  }

  const columnClass =
    "hp-p1-body hp-text-color-black-100 hp-text-color-dark-0 hp-font-weight-600 ";

  const columns = [
    {
      title: "Date",
      width: "5%",
      dataIndex: "index",
    },
    {
      title: "Date",
      width: "10%",
      render: (row) => (
        <span className={columnClass}>
          {moment(row.createdon).format("DD-MM-YYYY")}
        </span>
      ),
    },
    {
      title: "Code",
      render: (row) => <span className={columnClass}>{row.code}</span>,
      width: "10%",
    },

    {
      title: "Vehicle No",
      width: "10%",
      render: (row) => (
        <span className={columnClass}>
          {row.stock_purchase?.vehicle?.vehicleno}
        </span>
      ),
    },
    {
      title: "To Counter",
      width: "10%",
      render: (row) => (
        <span className={columnClass}>{row.tocounter?.name}</span>
      ),
    },
    {
      title: "From Counter",
      width: "10%",
      render: (row) => <span className={columnClass}>{row.counter.name}</span>,
    },
    {
      title: "Approval Status",
      width: "10%",
      render: (row) => (
        <span className={columnClass}>
          {
            <Text
              type={
                row.transferstatus == 2 || row.transferstatus == 4
                  ? "success"
                  : row.transferstatus == 3 || row.transferstatus == 5
                  ? "danger"
                  : "warning"
              }
            >
              {row.transferstatus_name == null
                ? "Pending"
                : row.transferstatus_name}
            </Text>
          }
        </span>
      ),
    },
    {
      title: "Actions",
      key: "action",
      width: "10%",
      render: (row, record) => (
        <Space size="middle">
          <Button
            onClick={() => {
              props.getStockTransferViewById({value: row.id});
              props.isStockApprovalModelVisible(true);
            }}
            className="hp-px-18 hp-btn-bg-color"
          >
            View
          </Button>
        </Space>
      ),
    },
  ];

  const data = stockApprovalList.map((row, key) => ({
    ...row,
    // index: key + 1,
    index:
      (props.stockApprovalparams.page - 1) *
        props.stockApprovalparams.page_size +
      (key + 1),
  }));

  return (
    
      <Row className="hp-mb-12">
        <Col span={24}>
          <Table
            columns={columns}
            rowClassName="hp-btn-bg-color-none"
            dataSource={data}
            scroll={{x: 1300}}
            pagination={false}
            size="middle"
          />
        </Col>
      </Row>
  );
}

function mapStateToProps({ stockRequest }) {
  return {
    noofpages: stockRequest.noofpages,
    listCount: stockRequest.listCount,
    RejectMode: stockRequest.RejectMode,
    stockApprovalList: stockRequest.stockApprovalList,
    stockApprovalparams: stockRequest.stockApprovalparams,
  };
}

export default connect(mapStateToProps, {
  getStockTransferViewById,
  isStockApprovalModelVisible,
  getStockApprovaltList,
  setStockApprovalParams,
})(StockTransferApprovalList);
