import React, { useEffect, useState } from "react";
import { connect } from "react-redux";
import {
  Row,
  Col,
  Divider,
  Form,
  Input,
  Button,
  Drawer,
  Space,
  Select,
  DatePicker,
} from "antd";
import moment, { min } from "moment";

import {
  isApprovalDrawerVisible,
  ApprovalFilterInputChangeValue,
  getStockApprovaltList,
  setStockApprovalParams,
} from "../../../../../../store/sideMenu/StockTransfer/StockRequest/stockrequestSlice";

var date = new Date(),
  y = date.getFullYear(),
  m = date.getMonth();

const filter = (props) => {
  const [form] = Form.useForm();
  useEffect(() => {}, []);

  const transferStatusList = [
    { id: 1, name: "Pending" },
    { id: 2, name: "Approved" },
    { id: 3, name: "Rejected" },
    { id: 4, name: "Accepted" },
    { id: 5, name: "To Counter Rejected" },
  ];

  const onFinish = () => {
    props.setStockApprovalParams({
      ...props.stockApprovalparams,
      start_date: props.stockApprovalparams.start_date,
      end_date: props.stockApprovalparams.end_date,
      page: 1,
      search: "",
      page_size: 10,
    });

    props.getStockApprovaltList({
      ...props.stockApprovalparams,
      // start_date:
      //   props.stockApprovalparams.start_date != null &&
      //   props.stockApprovalparams.start_date != ""
      //     ? moment(props.stockApprovalparams.start_date).format("YYYY-MM-DD")
      //     : "",
      // end_date:
      //   props.stockApprovalparams.end_date != null &&
      //   props.stockApprovalparams.end_date != ""
      //     ? moment(props.stockApprovalparams.end_date)
      //         .add(1, "days")
      //         .format("YYYY-MM-DD")
      //     : "",
      start_date: props.stockApprovalparams.start_date,
      end_date: props.stockApprovalparams.end_date,
      page: 1,
      search: "",
      page_size: 10,
    });
    props.isApprovalDrawerVisible(false);
  };

  function clearFilter() {
    props.setStockApprovalParams({
      start_date: "",
      end_date: "",
      page: 1,
      search: "",
      page_size: 10,
    });

    props.getStockApprovaltList({
      start_date: "",
      end_date: "",
      page: 1,
      search: "",
      page_size: 10,
    });
  }
  function disableDateRanges(range = { startDate: false, endDate: false }) {
    const { startDate, endDate } = range;
    return function disabledDate(current) {
      let startCheck = true;
      let endCheck = true;
      if (startDate) {
        startCheck =
          current &&
          current < moment(startDate + " 00:00:01", "YYYY-MM-DD HH:mm:ss");
      }
      if (endDate) {
        endCheck =
          current &&
          current >
            moment(
              moment(endDate).format("YYYY-MM-DD") + " 23:59:59",
              "YYYY-MM-DD HH:mm:ss"
            );
      }

      return (startDate && startCheck) || (endDate && endCheck);
    };
  }

  useEffect(() => {
    form.setFieldsValue({
      start_date: props.stockApprovalparams?.start_date,
      end_date: props.stockApprovalparams?.end_date,
      transferstatus: props.stockApprovalparams.transferstatus,
    });
  }, [props.stockApprovalparams]);

  function onSearch(val) {
    props.getStockApprovaltList({ page: 1, search: val, page_size: 10 });
  }
  return (
    <Row gutter={[24, 24]}>
      <Col span={24}>
        <Drawer
          title="Filters"
          width={420}
          onClose={() => {
            props.isApprovalDrawerVisible(false);
          }}
          placement="right"
          visible={props.stockApprovalDrawer}
          bodyStyle={{
            paddingBottom: 10,
          }}
        >
          <Form
            form={form}
            layout="vertical"
            name="basic"
            initialValues={{ remember: true }}
            onFinish={onFinish}
          >
            <Row gutter={[12, 12]}>
              <Col span={12}>
                <Form.Item label="From Date" name="start_date">
                  <DatePicker
                    style={{ width: "100%" }}
                    picker="date"
                    placeholder="Choose Date"
                    format="DD-MM-YYYY"
                    disabledDate={disableDateRanges({
                      endDate: new Date(),
                    })}
                    onChange={(date) => {
                      props.ApprovalFilterInputChangeValue({
                        key: "start_date",
                        value: date == null ? "" : date,
                      });
                      if (date == null) {
                        props.ApprovalFilterInputChangeValue({
                          key: "end_date",
                          value: "",
                        });
                      }
                    }}
                  />
                </Form.Item>
              </Col>

              <Col span={12}>
                <Form.Item label="To Date" name="end_date">
                  <DatePicker
                    style={{ width: "100%" }}
                    picker="date"
                    placeholder="Choose Date"
                    format="DD-MM-YYYY"
                    disabledDate={disableDateRanges({
                      endDate: moment().format("YYYY-MM-DD"),
                      startDate: moment(
                        props.stockApprovalparams?.start_date
                      ).format("YYYY-MM-DD"),
                    })}
                    disabled={
                      props.stockApprovalparams.start_date == null ||
                      props.stockApprovalparams.start_date == ""
                        ? true
                        : false
                    }
                    onChange={(date) => {
                      props.ApprovalFilterInputChangeValue({
                        key: "end_date",
                        value: date == null ? "" : date,
                      });
                    }}
                  />
                </Form.Item>
              </Col>

              <Col span={12}>
                <Form.Item label="Transfer Status" name="transferstatus">
                  <Select
                    showSearch
                    allowClear
                    placeholder="Select a Transfer Status"
                    optionFilterProp="transferstatus"
                    filterOption={(input, option) =>
                      option.children
                        .toLowerCase()
                        .includes(input.toLowerCase())
                    }
                    onChange={(value) => {
                      props.ApprovalFilterInputChangeValue({
                        key: "transferstatus",
                        value: value,
                      });
                    }}
                    value={
                      props.stockApprovalparams.transferstatus == null
                        ? ""
                        : props.stockApprovalparams?.transferstatus
                    }
                  >
                    {transferStatusList.map((transfer, index) => {
                      return (
                        <Option value={transfer.id}>{transfer.name}</Option>
                      );
                    })}
                  </Select>
                </Form.Item>
              </Col>
            </Row>
            <Row gutter={[12, 12]}>
              <Col md={12}>
                <Button
                  block
                  type="secondary"
                  className="hp-submit-btn hp-mt-10"
                  onClick={() => {
                    clearFilter();
                  }}
                  htmlType="button"
                >
                  Clear
                </Button>
              </Col>
              <Col md={12}>
                <Button
                  block
                  type="primary"
                  className="hp-submit-btn hp-mt-10"
                  onClick={() => {
                    form.submit();
                  }}
                >
                  Apply
                </Button>
              </Col>
            </Row>
          </Form>
        </Drawer>
      </Col>
    </Row>
  );
};

function mapStateToProps({ stockRequest }) {
  return {
    stockApprovalDrawer: stockRequest.stockApprovalDrawer,
    stockApprovalparams: stockRequest.stockApprovalparams,
  };
}

export default connect(mapStateToProps, {
  getStockApprovaltList,
  isApprovalDrawerVisible,
  setStockApprovalParams,
  ApprovalFilterInputChangeValue,
})(filter);
