import { Fragment, useState } from "react";

import { connect } from "react-redux";

import "flatpickr/dist/themes/light.css";
import { RiCloseFill } from "react-icons/ri";
import { Button, Modal, Row, Col, List, Popconfirm, Form, Input } from "antd";
import { RiErrorWarningLine } from "react-icons/ri";

import {
  isStockApprovalModelVisible,
  stockTransferApprove,
} from "../../../../../store/sideMenu/StockTransfer/StockRequest/stockrequestSlice";

export const isObjEmpty = (obj) => Object.keys(obj).length === 0;

const stockTransferApprovalModel = (props) => {
  const [remarks, setRemarks] = useState({});
  const [transferstatus, setTransferstatus] = useState(0);

  const [form] = Form.useForm();

  const handleResetInputValues = () => {
    form.resetFields();
    props.isStockApprovalModelVisible(false);
  };

  console.log("props.stockTransferDetails", props.stockTransferDetails);

  function stockInApprove() {
    props.stockTransferApprove({
      approveObj: {
        description: remarks.target?.value,
        transferstatus: transferstatus,
      },
      id: props.stockTransferDetails.id,
    });
    form.resetFields();
  }


  const stockTransferApprovalList = [
    {
      title: "TO COUNTER",
      description:
        props.stockTransferDetails.tocounter?.name == null
          ? "none"
          : props.stockTransferDetails.tocounter?.name,
    },
    {
      title: "FROM COUNTER",
      description:
        props.stockTransferDetails.counter?.name == null
          ? "none"
          : props.stockTransferDetails.counter?.name,
    },
    {
      title: "VEHICLE NUMBER",
      description:
        props.stockTransferDetails.stock_purchase?.vehicle?.vehicleno ==
        null
          ? "none"
          : props.stockTransferDetails.stock_purchase?.vehicle
              ?.vehicleno,
    },
    {
      title: "VERSION INFO",
      description:
        props.stockTransferDetails.stock_purchase?.vehicle?.version
          ?.name == null
          ? "none"
          : props.stockTransferDetails.stock_purchase?.vehicle?.version
              ?.name,
    },
    {
      title: "PURCHASE RATE",
      description:
        props.stockTransferDetails.stock_purchase?.purchaserate == null
          ? "none"
          : props.stockTransferDetails.stock_purchase?.purchaserate,
    },
    {
      title: "RE AMOUNT",
      description:
        props.stockTransferDetails.re_estimation_amount == null
          ? "none"
          : props.stockTransferDetails.re_estimation_amount,
    },
    {
      title: "REPAIR AMOUNT",
      description:
        props.stockTransferDetails.repair_amount == null
          ? "none"
          : props.stockTransferDetails.repair_amount,
    },
    {
      title: "SALE AMOUNT",
      description:
        props.stockTransferDetails.saleamount == null
          ? "none"
          : props.stockTransferDetails.saleamount,
    },
    {
      title: "PROFIT",
      description:
        props.stockTransferDetails.profit == null
          ? "none"
          : props.stockTransferDetails.profit,
    },
    {
      title: "Remarks",
      description:
        props.stockTransferDetails.description == null
          ? "none"
          : props.stockTransferDetails.description,
    },
    {
      title: "REQUESTED BY",
      description:
        props.stockTransferDetails.requestedby?.fullname == null
          ? "none"
          : props.stockTransferDetails.requestedby?.fullname,
    },
    {
      title: "APPROVED BY",
      description:
        props.stockTransferDetails.approvedby?.fullname == null
          ? "none"
          : props.stockTransferDetails.approvedby?.fullname,
    },

    
    {
      title: "ACCEPTED BY",
      description:
        props.stockTransferDetails.acceptedby?.fullname == null
          ? "none"
          : props.stockTransferDetails.acceptedby?.fullname,
    },
  ];

  const FooterAddOrUpdate = () => {
    return (
      <Fragment>
        <Popconfirm
          placement="topRight"
          title="Are you sure to Approve this Record?"
          onConfirm={() => {
            form.submit();
            setTransferstatus(2);
          }}
          okText="Yes"
          cancelText="No"
          icon={
            <RiErrorWarningLine className="remix-icon hp-text-color-primary-1" />
          }
        >
            <Button block className="hp-px-12 hp-accept-btn hp-font-weight-400">
            Approve
          </Button>
        </Popconfirm>
      </Fragment>
    );
  };

  const FooterCancel = () => {
    return (
      <Fragment>
        <Popconfirm
          placement="topRight"
          title="Are you sure to Reject this Record?"
          onConfirm={() => {
            form.submit();
            setTransferstatus(3);
          }}
          okText="Yes"
          cancelText="No"
          className="hp-ml-8"
          icon={
            <RiErrorWarningLine className="remix-icon hp-text-color-primary-1" />
          }
        >
          <Button block className="hp-px-12 hp-reject-btn hp-font-weight-400">
            Reject
          </Button>
        </Popconfirm>
      </Fragment>
    );
  };

  const modalTitle = (
    <h5 className="modal-title">Stock Transfer Approval View</h5>
  );
  // useEffect(() => {
  //   form.setFieldsValue({
  //     description: remarks.target?.value,
  //   });
  // }, [remarks]);

  return (
    <Modal
      visible={props.stockApprovalModel}
      title={modalTitle}
      onCancel={() => {
        handleResetInputValues();
        props.isStockApprovalModelVisible(false);
      }}
      className="hp-modal-p-24"
      footer={
        <>
          {props.stockTransferDetails.transferstatus == 1 && (
            <>
              {props.userAccessList?.indexOf("StockTransfer.approve_stocktransfer") !== -1 && (
                <Row justify="center">
                  <Col span={12} className="hp-pl-4">
                    <FooterAddOrUpdate />
                  </Col>

                  <Col span={12} className="hp-pr-4">
                    <FooterCancel />
                  </Col>
                </Row>
              )}
            </>
          )}
        </>
      }
      centered
      closeIcon={
        <RiCloseFill className="remix-icon text-color-black-100" size={24} />
      }
      width={516}
    >
      <Row>
        <Col span={24} md={24}>
          <List
            grid={{
              gutter: 16,
              xs: 1,
              sm: 2,
              md: 3,
              lg: 3,
              xl: 3,
              xxl: 3,
            }}
            dataSource={stockTransferApprovalList}
            renderItem={(item) => (
              <List.Item>
                <List.Item.Meta
                  title={<span>{item.title}</span>}
                  description={<span>{item.description}</span>}
                />
              </List.Item>
            )}
          />
          {props.stockTransferDetails.transferstatus == 1 && (
            <>
              <Form
                layout="vertical"
                form={form}
                onFinish={() => {
                  stockInApprove();
                }}
              >
                <Form.Item
                  name="description"
                  rules={[
                    {
                      required: true,
                      message: "Please Input description",
                    },
                  ]}
                  hasFeedback
                >
                  <Input.TextArea
                    id="description"
                    name="description"
                    style={{ width: "100%" }}
                    placeholder="Write Remarks..."
                    // value={remarks.target.value}
                    onChange={(e) => setRemarks(e)}
                  />
                </Form.Item>
              </Form>
            </>
          )}
        </Col>
      </Row>
    </Modal>
  );
};

function mapStateToProps({ stockRequest, system }) {
  return {
    stockApprovalModel: stockRequest.stockApprovalModel,
    stockTransferDetails: stockRequest.stockTransferDetails,
    userAccessList: system.userAccessList,
  };
}

export default connect(mapStateToProps, {
  isStockApprovalModelVisible,
  stockTransferApprove,
})(stockTransferApprovalModel);
