import React, { useState } from "react";
import { Link } from "react-router-dom";

import { Row, Col, Button, Card, Avatar, Typography } from "antd";
import { ArrowUpOutlined, ArrowDownOutlined } from "@ant-design/icons";
import { connect } from "react-redux";
import { RiErrorWarningLine } from "react-icons/ri";
import {
  isStockApprovalModelVisible,
  getStockTransferViewById,
} from "../../../../../store/sideMenu/StockTransfer/StockRequest/stockrequestSlice";

function StockTransferApprovalGrid(props) {
  const { value, stockType } = props;
  const { Text, Link } = Typography;

  return (
    <Col
      span={24}
      xs={24}
      md={12}
      xl={8}
      xxl={6}
      className="hp-mb-32"
      key={value.id}
    >
      <Card className="hp-border-color-black-40 hp-card-6 hp-eCommerceCardOne hp-eCommerceCardOne-wishlist">
        <Row>
          <Col span={24}>
            <Col className="hp-mb-16 hp-mt-0">
              <p className="hp-mb-0 hp-caption hp-font-weight-600 hp-text-color-black-80 hp-text-color-dark-80 hp-p1-body">
                Stock Details
              </p>

              <p className="hp-mb-4 hp-text-color-black-100 hp-mb-16 hp-text-color-dark-0 hp-p1-body hp-font-weight-500">
                Doc No : {value.code}
              </p>

              <p className="hp-mb-0 hp-caption hp-font-weight-400 hp-text-color-black-80 hp-text-color-dark-30">
                Vehicle Number
              </p>

              {value.stock_purchase !== null ? (
                <p className="hp-mb-4 hp-text-color-black-100 hp-mb-16 hp-text-color-dark-0 hp-p1-body hp-font-weight-500">
                  {value.stock_purchase?.vehicle?.vehicleno}
                </p>
              ) : (
                <p className="hp-mb-4 hp-text-color-black-100 hp-mb-16 hp-text-color-dark-0 hp-p1-body hp-font-weight-500">
                  None
                </p>
              )}

              <p className="hp-mb-0 hp-caption hp-font-weight-400 hp-text-color-black-80 hp-text-color-dark-30">
                Approval Status
              </p>

              <Text
                type={
                  value.transferstatus == 2 || value.transferstatus == 4
                    ? "success"
                    : value.transferstatus == 3 || value.transferstatus == 5
                    ? "danger"
                    : "warning"
                }
              >
                {value.transferstatus_name == null
                  ? "Pending"
                  : value.transferstatus_name}
              </Text>
            </Col>

            <Row gutter={[24, 24]} className="hp-mb-10">
              <Col span={8}>
                <Avatar size={55} icon={<ArrowUpOutlined />} />
              </Col>

              <Col span={16}>
                <p className="hp-mb-0 hp-caption hp-font-weight-400 hp-mb-5 hp-text-color-black-80 hp-text-color-dark-30">
                  To Counter
                </p>
                <p className="hp-mb-4 hp-text-color-black-100 hp-mb-0 hp-text-color-dark-0 hp-p1-body hp-font-weight-500">
                  {value.tocounter?.name}
                </p>
              </Col>
            </Row>
            <Row gutter={[24, 24]} className="hp-mb-10">
              <Col span={8}>
                <Avatar
                  className="hp-bg-color-danger-4"
                  size={55}
                  icon={
                    <ArrowDownOutlined className="hp-text-color-danger-1" />
                  }
                />
              </Col>

              <Col span={16}>
                <p className="hp-mb-0 hp-caption hp-font-weight-400 hp-mb-5 hp-text-color-black-80 hp-text-color-dark-30">
                  From Counter
                </p>

                <p className="hp-mb-4 hp-text-color-black-100 hp-mb-0 hp-text-color-dark-0 hp-p1-body hp-font-weight-500">
                  {value.counter?.name}
                </p>
              </Col>
            </Row>

            <Row gutter={[8, 8]}>
              <Col span={24} className="hp-w-100">
                <Button
                  block
                  onClick={() => {
                    props.getStockTransferViewById({value: value.id});
                    props.isStockApprovalModelVisible(true);
                  }}
                  className="hp-px-12 hp-btn-bg-color"
                >
                  View
                </Button>
              </Col>
            </Row>
          </Col>
        </Row>
      </Card>
    </Col>
  );
}

function mapStateToProps({ stockRequest }) {
  return {
    RejectMode: stockRequest.RejectMode,
  };
}

export default connect(mapStateToProps, {
  isStockApprovalModelVisible,
  getStockTransferViewById,
})(StockTransferApprovalGrid);
