import React, { useEffect, useState } from "react";
import { useSelector } from "react-redux";
import { Link } from "react-router-dom";
import { connect } from "react-redux";
import Swal from "sweetalert2";
import {
  Row,
  Col,
  List,
  Empty,
  Button,
  Input,
  Card,
  Pagination,
  Tabs,
  Spin,
} from "antd";
import { Search } from "react-iconly";
import StockTransferApprovalModel from "./StockTransferApprovalModel";
import {
  OrderedListOutlined,
  AppstoreOutlined,
  FilterOutlined,
  UploadOutlined,
} from "@ant-design/icons";
import PageTitle from "../../../../../layout/components/content/page-title";
import EmptyImage from "../../../../../assets/images/app/empty-list.svg";
import { isMobile } from "react-device-detect";
import {
  getStockApprovaltList,
  setStockApprovalParams,
  isModelVisible,
  isApprovalDrawerVisible,
} from "../../../../../store/sideMenu/StockTransfer/StockRequest/stockrequestSlice";
import StockTransferApprovalList from "./StockTransferApprovalList";
import StockTransferApprovalGrid from "./StockTransferApprovalGrid";
import Filter from "./Filters/Filter";

function stockTransferApprovals(props) {
  const { Search } = Input;
  const { TabPane } = Tabs;
  useEffect(() => {
    props.getStockApprovaltList({
      ...props.stockApprovalparams,
      page: 1,
      search: "",
      page_size: 10,
    });
  }, []);

  const [productListToggle, setProductListToggle] = useState(false);
  const [activeTab, setActiveTab] = useState(1);
  const [isModalVisible, setIsModalVisible] = useState(false);

  function onPageChange(page, pageSize) {
    props.setStockApprovalParams({
      ...props.stockApprovalparams,
      page_size: pageSize,
      page: page == 0 ? 1 : page,
    });
    props.getStockApprovaltList({
      ...props.stockApprovalparams,
      page_size: pageSize,
      page: page == 0 ? 1 : page,
    });
  }

  function onSearch(val) {
    props.setStockApprovalParams({
      ...props.stockApprovalparams,
      page: 1,
      search: val,
    });
    props.getStockApprovaltList({
      ...props.stockApprovalparams,
      page: 1,
      search: val,
    });
  }

  function showTotal(total) {
    return "Total " + total + " items";
  }

  function deleteRecord(record) {
    Swal.fire({
      title: "Are you sure?",
      text: "You won't be able to revert this!",
      icon: "warning",
      showCancelButton: true,
      confirmButtonColor: "#3085d6",
      cancelButtonColor: "#d33",
      confirmButtonText: "Yes, delete it!",
    }).then((result) => {
      if (result.isConfirmed) {
        // props.purchaseDelete(record);
      }
    });
  }

  const onChange = (key) => {
    setActiveTab(key);
    if (key == "1") {
      props.getStockApprovaltList({
        page: 1,
        search: "",
        page_size: 10,
      });
    } else {
      props.getStockApprovaltList({
        page: 1,
        search: "",
        page_size: 10,
      });
    }
    console.log(key);
  };

  return (
    <Row gutter={[12, 12]}>
      <PageTitle pageTitle="Stock Transfer Approvals" goBack={false} />

      <Col span={24}>
        <Row gutter={[12, 12]} className="hp-ecommerce-app-header ">
          {/* <Col md={12} xs={24} className="hp-ecommerce-app-header-search "> */}
          <Col span={24} xs={24} sm={24} md={16}  className="hp-ecommerce-app-header-search ">
            <Search
              placeholder="Search for date, doc, number, customer, counter, vehicle"
              allowClear
              onChange={(e) => {
                if (e.target.value == "") {
                  props.getStockApprovaltList({
                    ...props.stockApprovalparams,
                    page: 1,
                    search: "",
                  });
                  props.setStockApprovalParams({
                    ...props.stockApprovalparams,
                    page: 1,
                    search: "",
                  });
                }
              }}
              onSearch={(value) => {
                onSearch(value);
              }}
            />
          </Col>

          <Col span={24} xs={12} sm={12} md={4} >
            <Button
              onClick={() => {
                props.isApprovalDrawerVisible(true);
              }}
              className="hp-w-100 hp-p-12"
              icon={<FilterOutlined className="hp-mr-10" />}
            >
             {!isMobile && "Filter"}
            </Button>
          </Col>

          <Col span={24} xs={6} sm={6} md={2} >
            <Button
              className="hp-w-100 hp-p-12"
              icon={<AppstoreOutlined />}
              onClick={() => setProductListToggle(false)}
            ></Button>
          </Col>

          <Col span={24} xs={6} sm={6} md={2}>
            <Button
              className="hp-w-100 hp-p-12"
              icon={<OrderedListOutlined />}
              onClick={() => setProductListToggle(true)}
            ></Button>
          </Col>
        </Row>
      </Col>

      <Col
        span={24}
        xs={24}
        className="hp-purchase-app-content"
        productListToggle={productListToggle}
        setProductListToggle={setProductListToggle}
      >
        <Card>
          <Spin tip="loading" spinning={props.loading} delay={500}>
            {props.stockApprovalList.length !== 0 ? (
              <>
                {!productListToggle ? (
                  <>
                    <List
                      dataSource={props.stockApprovalList}
                      renderItem={(value) => (
                        <StockTransferApprovalGrid value={value} />
                      )}
                    />
                    <Pagination
                      size="small"
                      current={props.stockApprovalparams?.page}
                      pageSize={props.stockApprovalparams?.page_size}
                      total={props.listCount}
                      showTotal={showTotal}
                      showSizeChanger
                      onChange={onPageChange}
                      className="hp-mb-12 hp-mt-24 hp-float-right"
                    />
                  </>
                ) : (
                  <>
                    <StockTransferApprovalList
                      stockTransferList={props.stockApprovalList}
                      deletePurchase={deleteRecord}
                    />{" "}
                    <Pagination
                      size="small"
                      current={props.stockApprovalparams?.page}
                      pageSize={props.stockApprovalparams?.page_size}
                      total={props.listCount}
                      showTotal={showTotal}
                      showSizeChanger
                      onChange={onPageChange}
                      className="hp-mb-12 hp-mt-24 hp-float-right"
                    />
                  </>
                )}
              </>
            ) : (
              <Card>
                <Empty
                  className="hp-m-32"
                  image={EmptyImage}
                  imageStyle={{
                    height: 160,
                  }}
                  description={
                    <h5>Your Stock Transfer Approvals list is empty</h5>
                  }
                ></Empty>
              </Card>
            )}
          </Spin>
        </Card>
      </Col>
      <StockTransferApprovalModel />
      <Filter />
    </Row>
  );
}
function mapStateToProps({ stockRequest }) {
  return {
    stockApprovalList: stockRequest.stockApprovalList,
    noofpages: stockRequest.noofpages,
    listCount: stockRequest.listCount,
    loading: stockRequest.loading,
    stockApprovalparams: stockRequest.stockApprovalparams,
  };
}

export default connect(mapStateToProps, {
  getStockApprovaltList,
  isApprovalDrawerVisible,
  setStockApprovalParams,
  isModelVisible,
})(stockTransferApprovals);
